import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class ClientService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getClientList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/client`, { params: param });
  }

  addClient(data): Observable<any> {
    return this.http.post(`${this.API_URL}/client`, data);
  }

  editClient(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/client/${id}`, data);
  }

  getClientById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/client/${id}`, { params: param });
  }

}
