import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { TableViewModule } from 'src/app/common-module/table-view/table-view.module';
import { ClientListComponent } from './client-list/client-list.component';
import { AddClientComponent } from './add-client/add-client.component';
import { ClientService } from 'src/app/service/client.service';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';

const routes: Routes = [
  {
    path: "",
    component: ClientListComponent,
    data: { title: 'client_list' }
  },
  {
    path: "add",
    component: AddClientComponent,
    data: { title: 'add_client' }
  },
  {
    path: 'edit/:id',
    component: AddClientComponent,
    data: { title: 'edit_client' }
  },
]

@NgModule({
  declarations: [ClientListComponent, AddClientComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    TableViewModule,
    PaginationModule,
    NgSelectModule
  ], providers: [ClientService]
})

export class ClientModule { }
