declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ClientService } from 'src/app/service/client.service';
import { CommonService } from 'src/app/service/common.service';



@Component({
  selector: 'app-add-client',
  templateUrl: './add-client.component.html'
})
export class AddClientComponent implements OnInit {

  PageTitle = "Client";
  selectedFile: File;
  editImageUrl: any = null;
  addNewRadio: boolean = false
  categoryListArray: Array<any> = [];
  brandListArray: Array<any> = [];
  loadingState = false;
  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    client_code: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;
  countryListArray: any = [];

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private clientService: ClientService,
    private commonService: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      client_name: [null, Validators.compose([Validators.required])],
      client_code: [null, Validators.compose([Validators.required])],
      phone_number: [null, Validators.compose([Validators.required])],
      dial_code: ['+', Validators.compose([Validators.required])],
      email: [null, Validators.compose([Validators.required, Validators.email])],
      address: [null],
      country_name: [null],
      first_name: [null, Validators.compose([Validators.required])],
      last_name: [null, Validators.compose([Validators.required])],
      country_id: [null, Validators.compose([Validators.required])],
      tax_id: [null, Validators.compose([Validators.required])],
      state: [null, Validators.compose([Validators.required])],
      city: [null, Validators.compose([Validators.required])],
      street_address: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
    this.getCountryList();
    if (this.isEditing) {
      this.getEditObject();
    }
  }
  getCountryList() {
    this.commonService.getCountryList().subscribe((res: any) => {
      if (res.success) {
        this.countryListArray = res.data.country_list;
      }
    });
  }


  getEditObject() {
    this.clientService.getClientById(this.editId).subscribe((response) => {
      if (response.success) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            client_name: response.data.client_name,
            category_id: response.data.category_id,
            dial_code: response.data.user.dial_code,
            client_code: response.data.client_code,
            tax_id: response.data.tax_id,
            first_name: response.data.user.first_name,
            last_name: response.data.user.last_name,
            phone_number: response.data.user.phone_number,
            email: response.data.user.email,
            country_name: response.data.address.country_name,
            country_id: response.data.address.country_id,
            state: response.data.address.state,
            city: response.data.address.city,
            street_address: response.data.address.street_address ? response.data.address.street_address : ''
          });
      } else {
        this.router.navigateByUrl('/manage/client');
      }
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {

      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
      // this.editImageUrl = String(fileInput.target.value);
    }
  }

  removeFile() {
    this.selectedFile = null;
  }
  getCountryName(data) {
    this.addForm.patchValue({ country_name: data.label });
  }
  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('client_name', formData.value.client_name);
      data.append('client_code', formData.value.client_code);
      data.append('phone_number', formData.value.phone_number);
      data.append('first_name', formData.value.first_name);
      data.append('last_name', formData.value.last_name);
      data.append('dial_code', formData.value.dial_code);
      data.append('email', formData.value.email);
      data.append('tax_id', formData.value.tax_id);
      let address = {
        country_id: formData.value.country_id,
        country_name: formData.value.country_name,
        state: formData.value.state,
        city: formData.value.city,
        street_address: formData.value.street_address
      }
      data.append('address', JSON.stringify(address));

      if (this.selectedFile) {
        data.append('file', this.selectedFile);
      }
      if (this.isEditing) {
        this.clientService.editClient(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/client');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('client_code')) {
                this.formErrors['client_code'] = obj['client_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.clientService.addClient(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/client');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('client_code')) {
                this.formErrors['client_code'] = obj['client_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
}
